.form-group--eight {
    flex-direction: column;
    display: inline-flex;
    position: relative;

    .input_wrp {
        align-items: center;
    }

    .input-email {
        width: 696px;
    }

    .input-pass {
        width: 319px;
    }

    .eightname {
        margin-right: 47px;
    }

    .tooltip-hidden {
        left: 100px;
        bottom: -180px;
    }

    .tooltip-question {
        flex-basis: 100%;

        span {
            font-weight: 600;
        }
    }
}

@media only screen and (max-width : 992px) {
    .form-group--eight {
        .input-email {
            width: 100%;
        }

        .input_wrp {
            flex-wrap: wrap;

            label {
                margin-bottom: 10px;
            }
        }
    }

}


@media only screen and (max-width : 576px) {
    .form-group--eight {
        .input_wrp {

            label {
                font-size: 12px;
            }

            input {
                border: 1px solid $black;
            }

        }

        .input-pass {
            width: 155px;
        }

        .tooltip-hidden {
            border: 1px solid $black;
            left: 0;
            height: 70px;
            padding: 10px 15px;
            bottom: -80px;
            display: flex;
            align-items: center;

            &::after {

                width: 20px;
                height: 20px;
                background: url("../img/tooltip3.svg");
                bottom: 68px;
            }
        }

        .tooltip-question {
            font-size: 12px;
            margin-bottom: 0;


        }

    }

}
