.steps {
    display: flex;
}

.step_number {
    min-height: 600px;
    width: 75px;
    background: $black;
    color: #fff;
    font-size: 40px;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    border-right: 1px solid #fff;
    padding: 20px 15px 15px 15px;
    transition: all .3s;
    cursor: pointer;

    &:hover {
        opacity: .9;
    }

}

.step_number--five {
    border-right: none;

}

.step_content {
    position: relative;
    display: none;
    padding: 15px;
    background: #fff;
    border-top: 1px solid $black;
    border-bottom: 1px solid $black;
    border-right: 1px solid $black;
}



.ic_header {
    fill: #fff;
    width: 52px;
    height: 48px;
}

.active {
    display: flex;
    width: 100%;

    .step_number {
        flex-basis: 5%;

    }



    .step_content {
        display: block;
        flex-basis: 95%;


    }
}

@media only screen and (max-width : 1200px) {
    .steps {
        width: 100%;
    }

    .step {
        width: 20%;
    }

    .step_content {
        border-top: none;
        border-bottom: none;
        border-right: none;
        height: 85%;
    }


    .step_number {
        width: 100%;
        min-height: auto;
        height: 64px;
        align-items: center;

    }

    .active {

        .step_number {
            flex-basis: 100%;
            background: #fff;
        }

        .ic_header {
            fill: $black;
        }

        .step_content {
            display: block;
            width: 100%;
            position: absolute;
            top: 140px;
            left: 0;


        }
    }

    .ic_header {
        width: 24px;
        height: 24px;
    }

}

@media only screen and (max-width : 576px) {
    .step_content {

        padding: 10px;

    }
}
