.tooltip-hidden {
    transition: all .3s;
    position: absolute;
    z-index: 120;
    bottom: -200px;
    left: 0;
    background-color: #fff;
    width: 743px;
    height: 140px;
    padding: 40px 40px;
    border: 3px solid $black;

    &::after {
        content: '';
        width: 80px;
        height: 80px;
        background: url('../img/tooltip.svg');
        background-repeat: no-repeat;
        z-index: 140;
        display: block;
        position: absolute;
        left: 90px;
        bottom: 102px;

    }

}

.tooltip-info {
    display: flex;
    width: 90%;
    align-items: center;
    background: #fff;
    justify-content: space-between;
}

.tooltip-question {
    font-size: 21px;
    line-height: 1.3;
    flex-basis: 50%;
    margin-right: 15px;

}

.icon2 {
    &::after {
        left: 250px;


    }

}

.icon3 {
    &::after {
        left: 450px;
    }
}

.icon4 {
    left: auto;
    right: 0;

    &::after {
        left: 350px;
    }
}

.icon5 {
    left: auto;
    right: 0;

    &::after {
        left: auto;
        right: 120px;
    }
}

.icon6 {
    left: auto;
    right: 0;

    &::after {
        left: auto;
        right: 20px;
    }
}

.tooltip-inputs {
    display: flex;
    flex-basis: 50%;
    justify-content: flex-end;

}

.tooltip-inputs--center {
    flex-basis: 100%;
    justify-content: center;
    padding: 0 40px;

}

.tooltip-hidden--large {
    padding: 20px 20px 20px 50px;
    height: 180px;
    bottom: -240px;
    left: 20px;
    width: 1000px;


    &::after {
        left: 120px;
        bottom: 142px;
    }

    .tooltip-info {
        width: 100%;
        margin-bottom: 20px;


    }

    .tooltip-question {
        flex-basis: 80%;
        margin-right: 80px;
    }

    .tooltip-inputs {
        flex-basis: 20%;

    }
}

.icon7 {
    &::after {
        left: 670px;
    }

}

.icon8 {
    &::after {
        left: 870px;
    }

}

@media only screen and (max-width : 1519px) {
    .tooltip-hidden--large {
        left: 20px;
        width: 99%;


        &::after {
            left: 80px;
            bottom: 142px;
        }
    }

    .icon7 {
        &::after {
            left: 570px;
        }

    }

    .icon8 {
        &::after {
            left: auto;
            right: 90px;
        }

    }

}

@media only screen and (max-width : 1356px) {
    .icon3 {
        &::after {
            left: 350px;
        }
    }


}

@media only screen and (max-width : 1275px) {
    .tooltip-hidden {
        bottom: 20px;

        &::after {
            left: 130px;
        }
    }

    .icon2 {
        &::after {
            left: 390px;
        }

    }

    .icon3 {
        bottom: 40px;

        &::after {
            transform: rotateY(180deg);
            left: 480px;
        }

    }

    .icon4 {
        left: auto;
        right: 0;
        top: 40px;

        &::after {
            left: 40px;
            transform: rotate(180deg);
            top: 102px;
            transform: rotateX(180deg);
        }
    }

    .icon5 {
        left: auto;
        right: 0;
        top: 40px;

        &::after {
            left: 340px;
            transform: rotate(180deg);
            top: 102px;
            transform: rotateX(180deg);
        }
    }

    .icon6 {
        left: auto;
        right: 0;
        top: 40px;

        &::after {
            left: auto;
            right: 20px;
            transform: rotate(180deg);
            top: 102px;
            transform: rotateX(180deg);
        }
    }

    .tooltip-hidden--large {
        bottom: -240px;


        &::after {
            left: 80px;
            bottom: 142px;
        }
    }

    .icon7 {
        &::after {
            left: 420px;
        }

    }

    .icon8 {
        &::after {
            left: auto;
            right: 90px;
        }

    }
}

@media only screen and (max-width : 1200px) {
    .icon2 {
        bottom: 0;

        &::after {
            left: 280px;
            transform: rotateY(180deg)
        }

    }

    .icon3 {
        left: auto;
        right: 0;

        &::after {
            transform: rotateY(180deg);
            left: 380px;
        }

    }

    .icon4 {
        left: 0;
        right: auto;
        top: 40px;

        &::after {
            transform: rotate(180deg)
        }
    }
}

@media only screen and (max-width : 888px) {

    .tooltip-hidden--large {
        left: 20px;


        &::after {
            left: 80px;
            bottom: 142px;
        }

        .tooltip-question {
            font-size: 18px;
        }
    }

    .icon7 {
        &::after {
            left: 370px;
        }

    }

    .icon8 {
        &::after {
            left: auto;
            right: 90px;
        }

    }

}

@media only screen and (max-width : 758px) {
    .tooltip-info {
        justify-content: space-between;
    }

    .tooltip-hidden {
        bottom: 0px;
        width: auto;
        left: 50px;

    }

    .tooltip-question {
        font-size: 16px;
        line-height: 1.3;
        flex-basis: 50%;
        margin-right: 20px;
    }

    .tooltip-hidden--large {
        padding: 15px;
        bottom: -250px;


        &::after {
            left: 80px;
            bottom: 142px;
        }

        .tooltip-question {
            font-size: 16px;
            margin-right: 0;
            flex-basis: 100%;
            margin-bottom: 5px;
        }
    }

    .icon7 {
        &::after {
            left: 370px;
        }

    }

    .icon8 {
        &::after {
            left: auto;
            right: 90px;
        }

    }


}

@media only screen and (max-width : 690px) {
    .icon2 {
        bottom: 0;

        &::after {
            left: 280px;
            transform: rotateY(360deg)
        }

    }

    .icon7 {

        &::after {
            left: 270px;
        }

    }

}

@media only screen and (max-width : 550px) {

    .tooltip-question {
        margin-bottom: 20px;
        margin-right: 0;
        text-align: center;
        font-size: 15px;
    }

    .tooltip-info {
        flex-wrap: wrap;
        justify-content: center;
        width: 100%;
    }

    .tooltip-hidden {
        left: 0;
        bottom: 100px;
        padding: 20px 20px;


    }

    .tooltip-hidden--large {
        padding: 20px 3px;
        top: 180px;
        bottom: auto;
        height: 250px;

        &::after {
            bottom: 212px;
            left: 150px;
        }

        .tooltip-question {
            margin-bottom: 20px;
            font-size: 15px;
        }
    }

    .icon3 {
        bottom: -60px;

        &::after {
            left: 0px;
            right: auto;
        }
    }

    .icon4 {
        left: 0;
        right: auto;
        top: -50px;

        &::after {
            transform: rotate(180deg);
            left: auto;
            right: 40px;
        }
    }

    .icon5 {
        left: 0;
        right: auto;
        top: 100px;

        &::after {
            left: 20px;
            right: auto;
            transform: rotate(180deg)
        }
    }

    .icon6 {
        left: 0;
        right: auto;
        top: 140px;

        &::after {
            left: 120px;
            right: auto;
            transform: rotate(180deg)
        }
    }

    .icon7 {
        top: 300px;

        &::after {
            left: 170px;
        }

    }

    .icon8 {
        top: 430px;

        &::after {
            left: auto;
            right: 90px;
        }

    }

}

.tooltip-eleven {
    display: flex;
    align-items: center;

    .tooltip-question {
        flex-basis: 100%;
        margin-right: 0;

        a {
            color: $black;
            text-decoration: underline;
        }


    }

    .tooltip-info {
        width: 100%;

    }
}
