.step_wrp {
    display: flex;
    width: 100%;
    align-items: flex-start;
    position: relative;

}

.step-title {
    font-size: 34px;
    line-height: 1.3;
    margin-bottom: 64px;
    display: block;
}

@media only screen and (max-width : 1274px) {
    .step_wrp {
        flex-wrap: wrap;
        justify-content: space-around;
    }
}

@media only screen and (max-width : 550px) {


    .step-title {
        font-size: 17px;
        margin-bottom: 20px;

        span {
            font-size: 12px;
            line-height: 1.3;
        }

    }


}
