.twrp {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}


.ic_msp {
    height: 61px;
    width: 41px;
    fill: $black;
    margin-right: 15px;

}

.step-title--three {
    margin-bottom: 0;
}

.msp {

    padding: 20px;
    border: 3px solid $black;
    display: inline-block;
    font-size: 30px;
    color: $black;
    text-decoration: none;
    transition: all .3s;
    background: #fff;
    max-width: 491px;
    margin: 0 auto 32px auto;


    &__info {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &:hover {
        .ic_msp {
            fill: #fff;

        }

        color: #fff;
        background: $black;
    }

}

@media only screen and (max-width : 576px) {
    .msp {
        font-size: 17px;
        max-width: 100%;
        width: 100%;
        padding: 10px;

    }

    .ic_msp {
        height: 42px;
        width: 23px;


    }
}
