.range-slider {
    position: relative;
    width: 100%;
    text-align: center;


}

.progress {
    background: linear-gradient(to right, $black 40%, $black 40%, #fff 0%, #fff 100%);
    border: solid 2px $black;
    border-radius: 8px;
    height: 17px;
    width: 100%;
    outline: none;
    transition: background 450ms ease-in;
    -webkit-appearance: none;

}

.progress::-webkit-slider-thumb {
    width: 33px;
    height: 33px;
    border-radius: 50%;
    -webkit-appearance: none;
    cursor: pointer;
    background: #fff;
    border: 8px solid $black;
}

.progress::-moz-range-thumb {
    width: 23px;
    height: 23px;
    border-radius: 50%;
    -webkit-appearance: none;
    cursor: pointer;
    background: #fff;
    border: 10px solid $black;
}

.slider-wrp {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 190px;
    padding: 0 90px 0 90px;

}


.box-minmax {
    display: flex;
    justify-content: space-between;
    margin: 0 15px;
}

.rs-label {

    position: absolute;
    left: 220px;
    top: 60px;
    width: 252px;
    height: 90px;
    background: #BA205A;
    line-height: 1.3;
    text-align: center;
    padding-top: 10px;
    box-sizing: border-box;
    margin-left: -38px;

    color: #fff;
    font-size: 50px;



    &::before {
        content: '';
        display: block;
        width: 60px;
        height: 30px;
        background: url('../img/tooltip2.svg');
        background-repeat: no-repeat;
        margin-top: -32px;
        margin-left: 20px;

    }

}

.box-minmax {
    font-size: 30px;
    line-height: 1.3;
}

@media only screen and (max-width : 1300px) {
    .slider-wrp {

        padding: 0;

    }


}

@media only screen and (max-width : 576px) {

    .slider-wrp {

        margin-top: 40px;

    }

    .rs-label {
        width: 112px;
        height: 50px;
        font-size: 22px;
        left: 100px;

        &::before {
            content: '';
            display: block;
            width: 60px;
            height: 30px;
            background: url('../img/tooltip2.svg');
            background-repeat: no-repeat;
            margin-top: -32px;
            margin-left: 20px;

        }

    }

    .box-minmax {
        font-size: 16px;
        margin: 0 0;
        position: absolute;
    }

    .range-slider {

        margin-top: 80px;
        width: 100%;
    }

    .box-minmax--left {
        left: 15px;
    }

    .box-minmax--right {
        right: 15px;
    }
}
