.custom-checkbox {
    display: flex;
    width: 100%;
    margin: 0;
    cursor: pointer;
}

.checkbox-hidden {
    display: none;
}

.checkbox-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    max-width: 20px;
    flex: 0 0 20px;
    height: 20px;
    border-radius: 2px;
    border: 2px solid $black;
}

.custom-checkbox input:checked + .checkbox-wrap::after {
    transform: scale(1);
    opacity: 1;
}

.custom-checkbox .checkbox-wrap::after {
    content: "";
    display: block;
    width: 12px;
    height: 12px;
    /* background: url(../img/svg-check-ic.svg) no-repeat 0 0;*/
    background: #404D9C;
    background-size: 100%;
    will-change: transform;
    transform: scale(0);
    opacity: 0;
    transition: all 0.1s linear;
}


.checkboxes-form,
.personality {
    .custom-checkbox {
        margin-bottom: 20px;
        margin-top: 20px;
    }

    .checkbox-wrap {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 32px;
        max-width: 32px;
        flex: 0 0 32px;
        height: 32px;
        border-radius: 0px;
        border: 3px solid $black;
    }

    .custom-checkbox input:checked + .checkbox-wrap::after {
        transform: scale(1);
        opacity: 1;
    }

    .custom-checkbox .checkbox-wrap::after {
        content: "";
        display: block;
        width: 26px;
        height: 26px;
        /* background: url(../img/svg-check-ic.svg) no-repeat 0 0;*/
        background: #404D9C;
        background-size: 100%;
        will-change: transform;
        transform: scale(0);
        opacity: 0;
        transition: all 0.1s linear;

    }

    .checkbox-text {
        font-size: 29px;
        font-weight: 600;
        margin-left: 15px;
        margin-top: 5px;
    }


}

.personality {
    margin-top: -80px;
    margin-left: 98px;

    .checkbox-text {
        font-size: 18px;
        font-weight: 400;
        margin-left: 15px;
    }

}

.fname {
    margin-right: 32px !important;
}


@media only screen and (max-width : 1256px) {
    .personality {
        margin-top: -40px;
    }
}

@media only screen and (max-width : 576px) {
    .personality {
        .checkbox-text {
            font-size: 9px;
        }

    }

    .checkboxes-form,
    .personality {
        .custom-checkbox {
            margin-bottom: 10px;
            margin-top: 10px;
        }

        .checkbox-wrap {
            width: 15px;
            max-width: 15px;
            flex: 0 0 15px;
            height: 15px;
            border-radius: 0px;
        }

        .custom-checkbox .checkbox-wrap::after {
            width: 12px;
            height: 12px;
        }

        .checkbox-text {
            font-size: 15px;
            font-weight: 600;
            margin-left: 15px;
            margin-top: 0px;
        }




    }

    .form-group--part {
        .input_wrp label {
            font-size: 15px;

        }
    }

    .personality {
        margin-top: -20px;
    }

    .fname {
        margin-right: 25px !important;
    }
}



/*Радио кнопка*/
.input-hidden {
    display: none;
}

.ui-radiobox {
    display: block;
    margin-bottom: 0;
    cursor: pointer;
}

.ui-radiobox_custom {
    font-weight: 300;
    font-size: 18px;
    margin-right: 30px;

    &:last-child {
        margin-right: 0;
    }
}

.ui-radiobox__label {
    display: flex;
    align-items: center;
    position: relative;
    line-height: 1.4;
}

.ui-radiobox__label::before {
    width: 26px;
    flex: 0 0 26px;
    height: 26px;
    margin-right: 10px;
    background: #fff;
    border: 2px solid $black;
}

.ui-radiobox__label::before,
.ui-radiobox__label::after {
    content: "";
    display: block;

}

.ui-radiobox input:checked + .ui-radiobox__label::after {
    transform: scale(1);
}

.ui-radiobox__label::after {
    width: 20px;
    height: 20px;
    position: absolute;
    top: 3px;
    left: 3px;
    background: #404D9C;
    will-change: transform;
    transform: scale(0);
    transition: all 0.1s linear;
}


.ui-radiobox__label--red::after {
    width: 20px;
    height: 20px;
    position: absolute;
    top: 3px;
    left: 3px;
    background: #BA205A;
    will-change: transform;
    transform: scale(0);
    transition: all 0.1s linear;
}


.tooltip-inputs--center {


    .ui-radiobox_custom {
        font-weight: 300;
        font-size: 58px;
        margin-right: 100px;

        &:last-child {
            margin-right: 0;
        }
    }


    .ui-radiobox__label::before {
        width: 49px;
        flex: 0 0 49px;
        height: 49px;
        margin-right: 10px;
        background: #fff;
        border: 3px solid $black;
    }

    .ui-radiobox__label::after {
        width: 43px;
        height: 43px;
        top: 19px;
        left: 3px;

    }


    .ui-radiobox__label--red::after {
        width: 43px;
        height: 43px;
        top: 19px;
        left: 3px;

    }
}



@media only screen and (max-width : 550px) {
    .tooltip-inputs--center {

        .ui-radiobox_custom {
            font-weight: 300;
            font-size: 31px;
            margin-right: 30px;
        }


        .ui-radiobox__label::before {
            width: 30px;
            flex: 0 0 30px;
            height: 30px;
            margin-right: 10px;
            background: #fff;
            border: 3px solid $black;
        }

        .ui-radiobox__label::after {
            width: 24px;
            height: 24px;
            top: 10px;
            left: 3px;

        }


        .ui-radiobox__label--red::after {
            width: 24px;
            height: 24px;
            top: 10px;
            left: 3px;

        }
    }


}
