.s-title {
    font-size: 34px;
    font-weight: 400;
    margin-bottom: 10px;

}

.s-name {
    font-weight: 700;
    font-size: 34px;
    margin-bottom: 15px;
}

.s-info {
    display: flex;
    flex-direction: column;
    width: 78%;
}

.line_wrp {
    display: flex;
    align-items: center;
    flex-basis: 83%;
}


.s-info__delete {
    position: relative;
    padding: 20px 13px;
    border-left: 2px solid $black;
    cursor: pointer;
    flex-basis: 17%;
    margin-left: -10px;

}

.delete-txt {
    position: absolute;
    left: 10px;
    top: -14px;
    font-size: 14px;
    font-weight: 600;
}

.s-info__line {
    display: flex;
    flex-direction: row;
    width: 1100px;


}

.ic_seven {
    height: 45px;
    width: 45px;
}

.bottom-txt {
    font-size: 26px;
    margin: 10px 0 5px 0;
}

.s-info__date {

    font-size: 28px;
    font-weight: 600;
}

.s-info__icons {
    display: flex;
    padding: 0 29px;



    span {

        &:not(:last-child) {
            margin-right: 9px;
        }

    }
}





@media only screen and (max-width : 1280px) {
    .bottom-txt {
        font-size: 22px;

    }
}

@media only screen and (max-width : 1200px) {
    .active .step_number--seven {
        background: #000105;
        position: absolute;
        width: 100%;
        left: 0px;
        right: 0px;
        border-right: 0;
        border-left: none;
    }

    .step_content-seven {
        padding: 30px 20px 15px 50px;
        top: 80px !important;

    }
}

@media only screen and (max-width : 1098px) {
    .line_wrp {
        flex-wrap: wrap;
    }


    .s-info__line {
        display: flex;
        flex-direction: row;
        width: auto;
        margin-bottom: 20px;



    }

    .s-info__date {
        flex-basis: 100%;
        margin-bottom: 10px;

    }

    .s-info {
        display: flex;
        width: 100%;
        position: relative;

        &::after {
            content: '';
            width: 2px;
            height: 100vh;
            background: $black;
            right: 15%;
            position: absolute;
        }

    }

    .s-info__icons {

        padding: 0 0;
    }

    .line_wrp {
        flex-basis: 90%;
    }


    .s-info__delete {
        padding: 20px 13px;
        flex-basis: 10%;
        border-left: none;
        display: flex;
        align-items: center;



    }

    .bottom-txt {
        padding-right: 300px;
        line-height: 1.5;

    }


}





@media only screen and (max-width : 992px) {
    .step_content-seven {
        padding: 12px;
    }

    .s-info__date {

        font-size: 18px;
        font-weight: 600;
    }
}


@media only screen and (max-width : 838px) {

    .s-info__icons span:not(:last-child) {
        margin-right: 5px;
    }

    .icon img {
        height: 40px;
    }

    .bottom-txt {
        padding-right: 100px;
        line-height: 1.5;

    }

    .s-title {
        font-size: 24px;

    }

    .s-name {

        font-size: 24px;

    }

    .s-info__date {
        margin-bottom: 0;
    }

}


@media only screen and (max-width : 576px) {
    .icon img {
        height: 23px;
    }

    .ic_seven {
        height: 23px;
        width: 23px;
        padding-left: 4px;
    }

    .s-info__date {
        font-size: 14px;
        margin-bottom: 5px;
    }

    .s-title {
        font-size: 18px;
        font-weight: 400;
        margin-bottom: 10px;

      

    }

    .s-name {
        font-weight: 700;
        font-size: 18px;
        margin-bottom: 15px;
    }

    .delete-txt {

        left: 5px;
        top: -10px;
        font-size: 8px;

    }

    .s-info__delete {
        margin-left: 0;
        margin-top: 10px;
        justify-content: center;

    }

    .s-info__delete {
        padding: 0px 0px;
    }

    .s-info__line {

        margin-bottom: 14px;
    }

    .ic_six {
        width: 32px;
        height: 39px;
    }

    .s-info {

        &::after {

            right: 9%;

        }

    }

    .bottom-txt {
        font-size: 12px;
        padding-right: 60px;
        margin-top: 50px;
    }

}
