.burger-mobile {
    display: none;
    width: 130px;
    height: 40px;
}

.burger-mobile input {
    display: none;
}

.burger-mobile label {
    position: relative;
    width: 40px;
    height: 40px;
    display: block;
    cursor: pointer;
    background: transparent;
}

/* Exit Icon */

.burger-mobile label:before,
.burger-mobile input:checked + label:before {
    content: '';
    position: absolute;
    top: 50%;
    margin-top: -2px;
    width: 40px;
    height: 4px;
    border-radius: 2px;
    background: $black;
}

.burger-mobile label:before {
    -webkit-animation: animationOneReverse 1s ease forwards;
    animation: animationOneReverse 1s ease forwards;
}

@-webkit-keyframes animationOneReverse {
    0% {
        -webkit-transform: rotate(315deg);
    }

    25% {
        -webkit-transform: rotate(360deg);
    }

    50%,
    100% {
        -webkit-transform: rotate(0deg);
    }
}

@keyframes animationOneReverse {
    0% {
        transform: rotate(315deg);
    }

    25% {
        transform: rotate(360deg);
    }

    50%,
    100% {
        transform: rotate(0deg);
    }
}

.burger-mobile input:checked + label:before {
    -webkit-animation: animationOne 1s ease forwards;
    animation: animationOne 1s ease forwards;
}

@-webkit-keyframes animationOne {

    0%,
    50% {
        -webkit-transform: rotate(0deg);
    }

    75% {
        -webkit-transform: rotate(360deg);
    }

    100% {
        -webkit-transform: rotate(315deg);
    }
}

@keyframes animationOne {

    0%,
    50% {
        transform: rotate(0deg);
    }

    75% {
        transform: rotate(360deg);
    }

    100% {
        transform: rotate(315deg);
    }
}

.burger-mobile label:after,
.burger-mobile input:checked + label:after {
    content: '';
    position: absolute;
    top: 50%;
    margin-top: -2px;
    width: 40px;
    height: 4px;
    border-radius: 2px;
    background: $black;
}

.burger-mobile label:after {
    -webkit-animation: animationTwoReverse 1s ease forwards;
    animation: animationTwoReverse 1s ease forwards;
}

@-webkit-keyframes animationTwoReverse {
    0% {
        -webkit-transform: rotate(405deg);
    }

    25% {
        -webkit-transform: rotate(450deg);
    }

    50%,
    100% {
        -webkit-transform: rotate(0deg);
    }
}

@keyframes animationTwoReverse {
    0% {
        transform: rotate(405deg);
    }

    25% {
        transform: rotate(450deg);
    }

    50%,
    100% {
        transform: rotate(0deg);
    }
}

.burger-mobile input:checked + label:after {
    -webkit-animation: animationTwo 1s ease forwards;
    animation: animationTwo 1s ease forwards;
}

@-webkit-keyframes animationTwo {

    0%,
    50% {
        -webkit-transform: rotate(0deg);
    }

    75% {
        -webkit-transform: rotate(450deg);
    }

    100% {
        -webkit-transform: rotate(405deg);
    }
}

@keyframes animationTwo {

    0%,
    50% {
        transform: rotate(0deg);
    }

    75% {
        transform: rotate(450deg);
    }

    100% {
        transform: rotate(405deg);
    }
}

/* Burger Icon */

.burger-mobile label .burger:before {
    content: '';
    position: absolute;
    top: 6px;
    width: 40px;
    height: 4px;
    border-radius: 2px;
    background: $black;
    -webkit-animation: animationBurgerTopReverse 1s ease forwards;
    animation: animationBurgerTopReverse 1s ease forwards;
}

@-webkit-keyframes animationBurgerTopReverse {

    0%,
    50% {
        -webkit-transform: translateY(12px);
        opacity: 0;
    }

    51% {
        -webkit-transform: translateY(12px);
        opacity: 1;
    }

    100% {
        -webkit-transform: translateY(0px);
        opacity: 1;
    }
}

@keyframes animationBurgerTopReverse {

    0%,
    50% {
        transform: translateY(12px);
        opacity: 0;
    }

    51% {
        transform: translateY(12px);
        opacity: 1;
    }

    100% {
        transform: translateY(0px);
        opacity: 1;
    }
}

.burger-mobile input:checked + label .burger:before {
    -webkit-animation: animationBurgerTop 1s ease forwards;
    animation: animationBurgerTop 1s ease forwards;
}

@-webkit-keyframes animationBurgerTop {
    0% {
        -webkit-transform: translateY(0px);
        opacity: 1;
    }

    50% {
        -webkit-transform: translateY(12px);
        opacity: 1;
    }

    51%,
    100% {
        -webkit-transform: translateY(12px);
        opacity: 0;
    }
}

@keyframes animationBurgerTop {
    0% {
        transform: translateY(0px);
        opacity: 1;
    }

    50% {
        transform: translateY(12px);
        opacity: 1;
    }

    51%,
    100% {
        transform: translateY(12px);
        opacity: 0;
    }
}

.burger-mobile label .burger:after {
    content: '';
    position: absolute;
    bottom: 6px;
    width: 40px;
    height: 4px;
    border-radius: 2px;
    background: $black;
    -webkit-animation: animationBurgerBottomReverse 1s ease forwards;
    animation: animationBurgerBottomReverse 1s ease forwards;
}

@-webkit-keyframes animationBurgerBottomReverse {

    0%,
    50% {
        -webkit-transform: translateY(-12px);
        opacity: 0;
    }

    51% {
        -webkit-transform: translateY(-12px);
        opacity: 1;
    }

    100% {
        -webkit-transform: translateY(0px);
        opacity: 1;
    }
}

@keyframes animationBurgerBottomReverse {

    0%,
    50% {
        transform: translateY(-12px);
        opacity: 0;
    }

    51% {
        transform: translateY(-12px);
        opacity: 1;
    }

    100% {
        transform: translateY(0px);
        opacity: 1;
    }
}

.burger-mobile input:checked + label .burger:after {
    -webkit-animation: animationBurgerBottom 1s ease forwards;
    animation: animationBurgerBottom 1s ease forwards;
}

@-webkit-keyframes animationBurgerBottom {
    0% {
        -webkit-transform: translateY(0px);
        opacity: 1;
    }

    50% {
        -webkit-transform: translateY(-12px);
        opacity: 1;
    }

    51%,
    100% {
        -webkit-transform: translateY(-12px);
        opacity: 0;
    }
}

@keyframes animationBurgerBottom {
    0% {
        transform: translateY(0px);
        opacity: 1;
    }

    50% {
        transform: translateY(-12px);
        opacity: 1;
    }

    51%,
    100% {
        transform: translateY(-12px);
        opacity: 0;
    }
}

@media only screen and (max-width : 1200px) {
    .burger-mobile {
        display: block;
        margin-bottom: 10px;
        position: absolute;
        left: 15px;
        top: 15px;
    }

}
