.step_number--six {
    border-left: 1px solid #fff;
}

.step_content-six {
    padding: 20px;
}

.step-number--active-six {
    background: $black;
}

.step_content {
    p {
        font-size: 26px;
        line-height: 1.3;
        margin-bottom: 30px;
    }
}

.form-group {
    display: flex;
    justify-content: space-between;
    width: 100%
}

.fgp-left {
    flex-basis: 60%;
    margin-right: 24px;
}

.fgp-right {
    flex-basis: 40%
}

.input_wrp {
    margin-bottom: 20px;
    display: flex;

    label {
        font-size: 29px;
        margin-right: 15px;
        font-weight: 600;
    }

    input {
        width: 100%;
        border: 3px solid $black;
        height: 55px;
        font-size: 20px;
        padding-left: 20px;
    }
}

.btn-wrp {
    display: flex;
    justify-content: center;
    margin-top: 100px;
}

.btn-next {
    background: #fff;
    border: 3px solid $black;
    padding: 15px 28px;
    color: $black;
    cursor: pointer;
    font-size: 28px;
    transition: all .3s;

    &:hover {
        background: $black;
        color: #fff;
    }
}

.ic_number--six {
    width: 45px;
    height: 33px;
}

@media only screen and (max-width : 1200px) {
    .ic_six {
        width: 50px;
        height: 53px;
    }

    .step-six {
        position: absolute;
        width: 100%;
    }

    .step_content-six {
        top: 180px;
    }

    .active {
        .step_number--six {
            background: #000105;
            position: absolute;
            width: 100%;
            left: 0px;
            right: 0px;
            top: 65px;
            border-right: 0;
            border-left: none;
        }

        .step_content-six {
            top: 130px;
        }


    }
}

@media only screen and (max-width : 756px) {
    .form-group {
        flex-wrap: wrap
    }

    .step_content-six p {
        text-align: center;
    }

    .fgp-left {
        flex-basis: 100%;
        margin-right: 0px;
    }

    .fgp-right {
        flex-basis: 100%
    }

    .input_wrp--tel {
        margin-left: 100px;
    }

}

@media only screen and (max-width : 576px) {
    .step_content-six p {
        font-size: 13px;
    }

    .input_wrp {
        input {
            height: 26px;
        }
    }

    .personality .checkbox-text {
        font-size: 9px;
    }

    .input_wrp--tel {
        margin-left: 60px;
    }

    .personality {
        margin-left: 40px;
    }

    .btn-wrp {

        margin-top: 20px;
    }

    .btn-next {
        font-size: 16px;

    }


}
