.next-prev {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-top: 13px;
}

.ic_next {
    width: 28px;
    height: 32px;
}

.next {
    text-align: end;
}

.arrow {
    display: flex;
    align-items: center;
    color: $black;
    text-decoration: none;

    &:hover {
        opacity: .8;
    }

    span {
        margin: 0px 5px;
        font-size: 15px;
        color: $black;

    }
}

.next-prev--last {
    .next {
        opacity: 0;
    }
}

.next-prev--first {
    .prev {
        opacity: 0;
    }
}

@media only screen and (max-width : 1200px) {
    .next-prev {
        display: none;
    }

    .wrap {
        padding-left: 0;
        padding-right: 0;
    }

}